import React, { useState, useReducer } from "react";

import {
  Typography,
  Box,
  Button,
  Grid,
  ButtonGroup,
  Link
} from "@material-ui/core";
import useStyles from "../../styles";

import MonthSheet from "../../data/MonthSheet.json";
import DaySheet from "../../data/DaySheet.json";
import LastDaySheet from "../../data/LastDaySheet.json";

function CalendarWidget() {
  const classes = useStyles();
  const Today = new Date();

  // initialize calendar date with Today's information
  const initialState = {
    year: Today.getFullYear(),
    month: Today.getMonth(),
    date: Today.getDate()
  };

  // reducer
  const reducer = (state, action) => {
    switch (action.type) {
      case "INCREASE":
        if (state.month >= 11) {
          return { year: state.year + 1, month: 0 };
        } else {
          return { ...state, month: state.month + 1 };
        }
      case "DECREASE":
        if (state.month <= 0) {
          return { ...state, year: state.year - 1, month: 11 };
        } else {
          return { ...state, month: state.month - 1 };
        }
      case "SELECT_DATE":
        console.log(`select date`);
        return { ...state, date: action.date };
      default:
        throw new Error("unhandled action");
    }
  };
  const [state, dispatch] = useReducer(reducer, initialState);

  let DateForDisp = 0;
  const getFirstDate = () => {
    const firstDateForMonth = new Date(state.year, state.month, 1);
    return firstDateForMonth;
  };

  const DateDisplay = (WeekNumber, DayIndex) => {
    /// Init with First Date for each Month
    if (WeekNumber === 0) {
      if (DayIndex === getFirstDate().getDay()) {
        DateForDisp = 1;
      }
    }
    // Counting Date
    if (DateForDisp > 0 && DateForDisp <= LastDaySheet[state.month])
      return DateForDisp++;
  };

  const WeekArray = [0, 1, 2, 3, 4, 5];

  return (
    <div>
      <Box className={classes.rowBox} alignItems="center">
        <Typography>
          {MonthSheet[state.month] +
            " " +
            (state.year === Today.getFullYear() ? "" : state.year)}
        </Typography>
        <Box>
          <Button onClick={() => dispatch({ type: "DECREASE" })}>{"<"}</Button>
          <Button onClick={() => dispatch({ type: "INCREASE" })}>{">"}</Button>
        </Box>
      </Box>
      <Grid container>
        {DaySheet.map((day, index) => (
          <Grid item xs>
            <Box textAlign="center" key={index}>
              <Typography variant="body2">{day}</Typography>
            </Box>
          </Grid>
        ))}
      </Grid>
      {WeekArray.map((week, WeekNumber) => {
        return (
          <div key={WeekNumber}>
            <ButtonGroup fullWidth={true}>
              {DaySheet.map((day, DayIndex) => {
                return (
                  <DateButton
                    date={DateDisplay(WeekNumber, DayIndex)}
                    handleClick={() =>
                      dispatch({
                        type: "SELECT_DATE",
                        date: DateForDisp
                      })
                    }
                    day={day}
                    key={day}
                  />
                );
              })}
            </ButtonGroup>
          </div>
        );
      })}
      <p>{state.year}</p>

      <p>{state.month}</p>

      <p>{state.date}</p>
    </div>
  );
}

const DateButton = ({ date, selectDate, day, handleClick }) => {
  const classes = useStyles();

  if (date === undefined) {
    return (
      <Button className={classes.dateButton} fullWidth disabled>
        {""}
      </Button>
    );
  } else {
    if (day === "Sun") {
      return (
        <Button className={classes.dateButton} fullWidth color="secondary">
          {date}
        </Button>
      );
    } else if (day === "Sat") {
      return (
        <Button className={classes.dateButton} fullWidth color="primary">
          <div onClick={() => handleClick()}>{date}</div>
        </Button>
      );
    }
  }
  return (
    // <div className="calendar-widget-datebutton">
    <Button className={classes.dateButton} fullWidth>
      {date}
    </Button>
  );
};

export default CalendarWidget;
